.features-container {
    background-color: $secondary;
    color: #fff;
    padding-block: 8.75rem;
    position: relative;

    .bottom-right-bg {
        position: absolute;
        top: 0;
        right: -100px;
        z-index: 0;

        @media screen and (min-width: 768px) {
            top: unset;
            bottom: 0;
        }
    }
    
    .main-layout {
        display: flex;
        flex-direction: column;
        gap: 7.8rem;
        position: relative;
        z-index: 1;

        @media screen and (min-width:768px) {
            flex-direction: row;
        }
    }

    .features-heading-container {
        font-size: 2rem;
        font-weight: 700;

        @media screen and (min-width: 768px) {
            font-size: 3rem;
        }
    }

    .feature-preview {
        display: flex;
        flex-direction: column;
        text-align: center;
        gap: 1.4rem;
        margin-bottom: 2rem;

        @media screen and (min-width:540px) {
            flex-direction: row;
            text-align: start;
        }

        h4 {
            color: $accent-pink;
            margin-bottom: 1rem;
            font-size: 1.125rem;
        }

        p {
            font-size: 0.93rem;
        }
    }
}