.app-header {
    background-color: $primary;
    color: #fff;
    padding-top: 4.6rem;
    
    .secondary-layout { 
        display: flex;
        justify-content: space-between;
    }

    .logo-nav-container {
        display: flex;
        align-items: center;

        .logo {
            margin-inline-end: 5rem;
            width: 160px;
            height: 40px;
        }

        nav ul {
            display: flex;
            gap: 2.5rem;
            display: none;
        }
    }

    .hamburger {
        display: block;
        width: 20px;
        height: 17px;
    }

    .btn {
        border: 1px solid #fff;
        color: #fff;
        display: none;
    }

    @media screen and (min-width: 650px) {
        .logo-nav-container {

            nav ul {
                display: flex;
            }
        }

        .btn {
            display: block;
        }

        .hamburger {
            display: none;
        }
    }
}