.call-to-action {
    background-color: $accent-pink;
    color: $primary;
    padding-block: 80px;
    position: relative;

    .main-layout {
        display: flex;
        flex-direction: column;
        text-align: center;
        align-items: center;
        justify-content: space-between;
        
        @media screen and (min-width: 375px) {
            flex-direction: row;
            text-align: start;
            
        }
    }

    h2 {
        font-size: 3rem;
    }

    .btn {
        color: $primary;
        border: 1px solid $primary;
    }

    .bottom-left-bg {
        position: absolute;
        bottom: 0;
        left: 0;
    }
}