.app-hero {
    background-color: $primary;
    color: #fff;
    padding-block: 8.125rem;
    position: relative;

    .left-mid-bg {
        position: absolute;
        left: -100px;
        // top: 50%;
        z-index: 0;
    }

    .main-layout {
        display: flex;
        flex-direction: column;
        text-align: center;
        gap: 3rem;

        @media screen and (min-width: 768px) {
            flex-direction: row;
            text-align: start;
        }

        .bottom-right-bg {
            position: absolute;
            right: 0;
            bottom: 0;
            z-index: 0;
        }
    }

    .hero-heading-container {
        font-size: 2.5rem;
        line-height: 0.9;
        flex-basis: 50%;
        position: relative;
        z-index: 1;
        
        @media screen and (min-width: 375px) {
            font-size: 4rem;
        }
        
        @media screen and (min-width: 768px) {
            font-size: 4.25rem;
        }
        
        span {
            color: $accent-pink;
        }
    }
    
    .hero-desc-container {
        flex-basis: 50%;
        font-size: 1.125rem;
        align-self: flex-end;
    }
}