.main-layout {
    max-width: 327px;
    margin: auto;
    padding-inline: 24px;
    
    @media screen and (min-width:375px) {
        max-width: 573px;
        padding-inline: 100px;
    }
    
    @media screen and (min-width:768px) {
        max-width: 1110px;
        padding-inline: 0;
    }
}

.secondary-layout {
    max-width: 327px;
    margin: auto;
    padding-inline: 24px;
    
    @media screen and (min-width:375px) {
        max-width: 689px;
        padding-inline: 40px;
    }
    
    @media screen and (min-width:768px) {
        max-width: 1110px;
        padding-inline: 0;
    }
}