.testimonials-container {
    background-color: $primary;
    color: #fff;
    padding-block: 140px;
    text-align: center;
    position: relative;

    .testimonials-heading-container {
        font-size: 1.125rem;
        font-weight: 700;
        margin-bottom: 5.75rem;

        span {
            color: $accent-blue;
        }
    }

    .top-left-bg {
        position: absolute;
        top: 0;
        left: 0;
    }

    .bottom-right-bg {
        position: absolute;
        bottom: 0;
        right: 0;
    }

    .testimonail-list {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 1.875rem;

        @media screen and (min-width: 768px) {
            flex-direction: row;
        }

        .testimonial-preview {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            position: relative;
            
            .testimonial {
                font-weight: 600;
                font-size: 0.95rem;
                margin-bottom: 1.5rem;
                position: relative;
                z-index: 1;
            }

            h4 {
                color: $accent-blue;
                font-size: 1.125;
                margin-bottom: 3.25rem;
            }

            .quote-icon {
                position: absolute;
                top: -40px;
                z-index: 0;
            }

            .avatar {
                border-radius: 50%;
                width: 62px;
                height: 62px;
                border: 2px solid $accent-blue;
            }
        }
    }
}